.modal__call {
  visibility: hidden;
  width: 490px;
  height: 940px;
  box-shadow: 1px 0px 20px #dddddd;
  position: fixed;
  top: 0;
  right: 0;
  background: #FFFFFF; }
  .modal__call-title {
    font-size: 28px;
    padding: 24px 0;
    position: relative;
    margin-left: 12px;
    font-weight: 700; }
  .modal__call-title::before {
    content: "";
    width: 4px;
    height: 40px;
    border-top-right-radius: 4px;
    border-bottom-right-radius: 4px;
    background-color: #41f6d7;
    position: absolute;
    left: -11px;
    top: 19px; }
  .modal__call-inp {
    width: 258px;
    height: 46px;
    border-radius: 9px 9px;
    padding-left: 16px;
    margin: 0 32px;
    border: 1px solid #EAEAEA; }
  .modal__call--text {
    width: 376px;
    height: 48px;
    margin: 16px 32px 40px; }
  .modal__call--text > span {
    color: #FF3E79; }
  .modal__call--button {
    display: flex;
    justify-content: space-around;
    align-items: center;
    margin-left: 25px;
    width: 119px;
    height: 32px;
    margin-top: 16px;
    margin-bottom: 16px;
    margin-left: auto;
    background: #FF3E79;
    border-style: none;
    border-radius: 16px;
    font-weight: bold;
    font-size: 12px;
    line-height: 24px;
    letter-spacing: -0.15px;
    text-transform: uppercase;
    color: #fff;
    cursor: pointer; }
  @media (min-width: 1440px) {
    .modal__call__close-button {
      margin-left: -50px;
      margin-top: 40px; } }
  @media (min-width: 768px) {
    .modal__call__close-button {
      margin-left: -50px;
      margin-top: 40px; } }
  @media (min-width: 320px) {
    .modal__call__close-button {
      margin-left: 10px;
      margin-top: 7px; } }

@media (min-width: 320px) {
  .modal__call {
    width: 320px;
    background: #FFFFFF; }
    .modal__call-inp {
      width: 273px;
      margin: 0 16px; }
    .modal__call--text {
      width: 286px;
      height: 64px; } }

.modal__call__close-button {
  margin-left: 10px;
