.menu:not(:nth-child(2)) {
  padding: 0 16px; }

@media (min-width: 768px) {
  .menu:not(:nth-child(2)) {
    padding: 0 63px; } }

.menu {
  display: flex;
  justify-content: space-between; }
  @media (min-width: 768px) {
    .menu {
      display: flex;
      justify-content: flex-end;
      padding: 42px 32px 0 0; } }
  .menu__logo {
    margin-right: auto;
    margin-left: 15px; }
    @media (min-width: 1120px) {
      .menu__logo {
        display: none; } }
  .menu__repair {
    margin-right: 15px;
    position: relative; }
  .menu__call, .menu__chat, .menu__profile {
    display: none; }
    @media (min-width: 768px) {
      .menu__call, .menu__chat, .menu__profile {
        display: block;
        margin-right: 15px; } }
    @media (min-width: 1120px) {
      .menu__call, .menu__chat, .menu__profile {
        display: none; } }
  .menu__span {
    display: none; }
    @media (min-width: 1120px) {
      .menu__span {
        display: inline-block;
        padding-right: 8px; } }
  .menu__repair {
    margin-right: 15px;
    position: relative; }
    @media (min-width: 768px) {
      .menu__repair {
        padding-left: 27px; } }
    @media (min-width: 1120px) {
      .menu__repair {
        display: flex;
        align-items: center; } }
  .menu__repair::before {
    content: "";
    height: 32px;
    width: 2px;
    background-color: #eaeaea;
    left: -50%;
    top: 10px;
    position: absolute; }
    @media (min-width: 768px) {
      .menu__repair::before {
        left: 6%; } }
    @media (min-width: 1120px) {
      .menu__repair::before {
        display: none; } }
  @media (min-width: 768px) {
    .menu__burger {
      position: relative;
      padding-right: 27px; } }
  @media (min-width: 1120px) {
    .menu__burger {
      display: none; } }
  @media (min-width: 768px) {
    .menu__burger::after {
      content: "";
      height: 32px;
      width: 2px;
      background-color: #eaeaea;
      right: 5%;
      top: 10px;
      position: absolute; } }
  @media (min-width: 1120px) {
    .menu__checkstatus {
      display: flex;
      align-items: center; } }
