.prices {
  background: #F2F2F2; }
  .prices__title {
    margin-bottom: 16px;
    font-weight: 400;
    padding: 16px;
    color: #7E7E82;
    background: linear-gradient(to right, #F2F2F2 30%, #FFFFFF);
    border-top: 1px solid #D9FFF5;
    text-transform: uppercase;
    line-height: 24px;
    font-size: 16px; }
  .prices__item {
    position: relative;
    flex-direction: column;
    align-items: flex-start;
    margin: 16px 16px 40px;
    padding: 16px;
    width: 240px;
    height: 160px;
    border-style: none;
    background: #FFFFFF; }
  .prices__name {
    margin: 0;
    font-size: 14px;
    line-height: 16px;
    font-weight: 500;
    color: #7E7E82;
    mix-blend-mode: normal;
    opacity: 0.72; }
  .prices__description {
    margin: 0;
    font-size: 14px;
    line-height: 18px;
    letter-spacing: 0.2px;
    color: #1b1c21; }
  .prices__button {
    position: absolute;
    bottom: 16px;
    right: 9px;
    display: flex;
    justify-content: space-around;
    align-items: center;
    width: 119px;
    height: 32px;
    background: #FF3E79;
    border-style: none;
    border-radius: 16px;
    font-weight: bold;
    font-size: 12px;
    line-height: 24px;
    letter-spacing: -0.15px;
    text-transform: uppercase;
    color: #fff;
    cursor: pointer; }
  .prices__terms {
    max-width: 480px;
    padding: 0 16px;
    margin-bottom: 18px;
    font-size: 14px;
    line-height: 20px;
    letter-spacing: 0.2px;
    color: #EAEAEA; }
  .prices__offer {
    display: inline-block;
    padding: 0 16px;
    margin-bottom: 40px;
    font-weight: 500;
    color: #EAEAEA;
    font-size: 16px;
    line-height: 24px;
    letter-spacing: -0.2px;
    transition: color 0.3s; }
  .prices__offer > img {
    margin-left: 8px;
    vertical-align: middle; }
  .prices__table {
    display: none; }

@media (min-width: 768px) {
  .prices__cards {
    display: none; } }

@media (min-width: 768px) and (min-width: 768px) {
  .prices__title {
    padding-left: 38px; } }

@media (min-width: 768px) and (min-width: 1120px) {
  .prices__title {
    padding-left: 52px; } }

@media (min-width: 768px) {
  .prices__terms {
    padding: 0 24px; }
  .prices__offer {
    padding: 0 24px; }
  .prices__table {
    display: table;
    border-collapse: collapse;
    margin: 0 auto;
    margin-bottom: 24px;
    padding: 24px;
    min-width: 720px;
    width: 100%;
    white-space: nowrap; } }

@media (min-width: 1120px) {
  .prices__cards {
    display: none; }
  .prices__cards {
    display: none; }
  .prices__terms {
    padding: 0 32px; }
  .prices__offer {
    padding: 0 32px; } }

@media (min-width: 768px) {
  .table-wrapper {
    padding: 0 45px; }
  .table__head {
    text-align: left;
    font-size: 14px;
    font-weight: normal;
    line-height: 16px;
    color: #7E7E82;
    mix-blend-mode: normal;
    opacity: 0.72; }
  .table__row {
    position: relative; }
  .table__row:not(:last-of-type)::after {
    position: absolute;
    content: "";
    display: block;
    left: 16px;
    bottom: 0;
    width: 95%;
    height: 1px;
    background-color: #EAEAEA; }
  .table__header {
    padding: 16px 16px;
    font-weight: 500; }
  .table__body {
    background-color: #fff; }
  .table__data {
    font-size: 14px;
    line-height: 18px;
    letter-spacing: 0.2px;
    padding-left: 16px; }
  .table__data:last-of-type {
    padding-right: 16px; }
  .table__button {
    display: flex;
    justify-content: space-around;
    align-items: center;
    width: 119px;
    height: 32px;
    margin-top: 16px;
    margin-bottom: 16px;
    margin-left: auto;
    background: #FF3E79;
    border-style: none;
    border-radius: 16px;
    font-weight: bold;
    font-size: 12px;
    line-height: 24px;
    letter-spacing: -0.15px;
    text-transform: uppercase;
    color: #fff;
    cursor: pointer; } }
