.main {
  background: #F8F8F8; }
  .main__heading {
    font-size: 28px;
    padding: 24px 0;
    position: relative;
    margin-left: 12px;
    font-weight: 700; }
    @media (min-width: 768px) {
      .main__heading {
        padding-left: 20px;
        padding-bottom: 37.5px; } }
    @media (min-width: 1440px) {
      .main__heading {
        margin: 42px 450px 0 32px;
        padding-bottom: 0;
        position: absolute;
        top: -30px; } }
  .main__text-content--hidden {
    display: none; }
  .main__heading::before {
    content: "";
    width: 4px;
    height: 40px;
    border-top-right-radius: 4px;
    border-bottom-right-radius: 4px;
    background-color: #41f6d7;
    position: absolute;
    left: -12px;
    top: 19px; }
    @media (min-width: 768px) {
      .main__heading::before {
        left: -12px; } }
    @media (min-width: 1440px) {
      .main__heading::before {
        width: 3px;
        left: -34px;
        top: 22px; } }
  .main__scroll-menu {
    display: flex;
    justify-content: space-between;
    overflow-x: scroll;
    white-space: nowrap;
    margin-left: 16px; }
    @media (min-width: 1440px) {
      .main__scroll-menu {
        overflow-x: clip;
        flex-wrap: wrap;
        padding: 8.37px 47px 14.63px 45px;
        margin: 0; } }
  .main__scroll-menu-link {
    text-decoration: none;
    color: #7e7e82;
    line-height: 43px;
    letter-spacing: -0.2px;
    font-weight: 500;
    padding: 8px 16px; }
  .main__scroll-menu-link:focus {
    border: 2px solid #b8ffec;
    background-color: white;
    border-radius: 6px; }
  .main__text {
    font-size: 14px;
    line-height: 18px;
    padding: 24px 16px 0;
    font-weight: 400; }
    @media (min-width: 768px) {
      .main__text {
        order: 1;
        width: 312px;
        height: 160px;
        font-size: 14px;
        line-height: 20px;
        padding: 32px 48px 88px 24px;
        display: block; } }
    @media (min-width: 1440px) {
      .main__text {
        width: 490px;
        padding: 19.32px 0 13.78px -2px; } }
  @media (min-width: 768px) {
    .main__text-content-span {
      display: none; } }
  @media (min-width: 1440px) {
    .main__text-content-span {
      display: block; } }
  .main__text-content:nth-child(2) {
    display: none; }
    @media (min-width: 768px) {
      .main__text-content:nth-child(2) {
        display: block;
        padding-top: 20px; } }
  .main__content-btn {
    padding: 16px 113px 16px 8px;
    display: flex;
    align-items: center; }
    @media (min-width: 768px) {
      .main__content-btn {
        order: 3;
        padding: 10px 173px 54px 9px; } }
  .main__btn-img {
    padding-right: 16px; }
    @media (min-width: 768px) {
      .main__btn-img {
        padding-right: 16px; } }
  .main__img {
    width: 100%; }
    @media (min-width: 768px) {
      .main__img {
        order: 2;
        width: 360px;
        padding: 8px 8px 27px 0; } }
    @media (min-width: 1440px) {
      .main__img {
        width: 444.35px; } }
  @media (min-width: 768px) {
    .main__container {
      display: flex;
      flex-wrap: wrap; } }
  @media (min-width: 1440px) {
    .main__container {
      flex-wrap: nowrap;
      justify-content: space-evenly; } }
  @media (min-width: 1440px) {
    .main__scroll-menu-item:last-child {
      flex-basis: 100%; } }
