.brand {
  min-width: 320px;
  padding-bottom: 24px;
  border-bottom: 1px solid #D9FFF5;
  background: #F2F2F2; }
  .brand__title {
    font-weight: 400;
    padding: 16px;
    color: #7E7E82;
    background: linear-gradient(to right, #F2F2F2 30%, #FFFFFF);
    border-top: 1px solid #D9FFF5;
    text-transform: uppercase;
    line-height: 24px;
    font-size: 16px; }
    @media (min-width: 768px) {
      .brand__title {
        padding-left: 38px;
        height: 23px; } }
    @media (min-width: 1120px) {
      .brand__title {
        padding-left: 52px; } }
  .brand__slider-img {
    padding: 16px 0 25px 16px; }
    @media (min-width: 768px) {
      .brand__slider-img {
        padding: 18px 0 0px 31px;
        width: 224px;
        height: 72px; } }
  .brand-btn {
    display: none; }
    @media (min-width: 768px) {
      .brand-btn {
        display: block;
        background-color: inherit;
        margin-left: 44px;
        margin-bottom: 34px;
        font-weight: 500;
        font-size: 16px;
        letter-spacing: -0.2px;
        color: #1B1C21;
        display: flex;
        align-items: center; } }
  .brand__slider {
    display: flex;
    flex-basis: 240px;
    padding-bottom: 11px; }
  @media (min-width: 768px) {
    .brand__slider-img {
      padding: 18px 0 0px 31px;
      width: 224px;
      height: 72px; } }
  @media (min-width: 1120px) {
    .brand__slider-img {
      padding: 17px 0 0px 44px; } }
  @media (min-width: 768px) {
    .brand__slider--hidden {
      display: none; } }
  @media (min-width: 768px) {
    .brand_btn-img {
      margin-right: 17px; } }
  @media (min-width: 768px) {
    .brand__slider--visible {
      display: block; } }
  @media (min-width: 768px) {
    .brand__list {
      display: flex;
      flex-wrap: wrap; } }
  @media (min-width: 1400px) {
    .brand__slider:nth-child(7), .brand__slider:nth-child(8) {
      display: block; } }
