.repair {
  min-width: 320px;
  padding-bottom: 24px;
  border-bottom: 1px solid #D9FFF5;
  background: #F2F2F2; }
  .repair__span {
    font-weight: 500;
    font-size: 16px;
    line-height: 24px;
    letter-spacing: -0.2px;
    color: #1B1C21;
    position: absolute;
    left: 10px;
    top: 32px; }
    @media (min-width: 768px) {
      .repair__span {
        right: 64px; } }
    @media (min-width: 1120px) {
      .repair__span {
        right: 68px;
        top: 15px; } }
  .repair__title {
    font-weight: 400;
    padding: 16px;
    color: #7E7E82;
    background: linear-gradient(to right, #F2F2F2 30%, #FFFFFF);
    border-top: 1px solid #D9FFF5;
    text-transform: uppercase;
    line-height: 24px;
    font-size: 16px; }
    @media (min-width: 768px) {
      .repair__title {
        padding-left: 38px;
        height: 23px; } }
    @media (min-width: 1120px) {
      .repair__title {
        padding-left: 52px; } }
  .repair__slider-img {
    padding: 16px 0 25px 16px; }
    @media (min-width: 768px) {
      .repair__slider-img {
        padding: 20px 0 0px 12px; } }
  .repair__btn {
    display: none; }
    @media (min-width: 768px) {
      .repair__btn {
        display: block;
        background-color: inherit;
        margin: 20px 0 34px 44px;
        font-weight: 500;
        font-size: 16px;
        letter-spacing: -0.2px;
        color: #1B1C21;
        display: flex;
        align-items: center; } }
  .repair__slider {
    display: flex;
    padding-bottom: 11px;
    min-width: 224px;
    min-height: 160px;
    margin-left: 31px;
    margin-top: 16px;
    background-color: #FFFFFF;
    border-radius: 7px;
    position: relative;
    margin-bottom: 40px; }
    @media (min-width: 768px) {
      .repair__slider {
        margin-bottom: 0;
        min-width: 218px; } }
    @media (min-width: 1120px) {
      .repair__slider {
        margin-left: 44px; } }
    .repair__slider__item:first-child {
      margin-left: 25px; }
  @media (min-width: 768px) {
    .repair__slider-img {
      padding: 20px 0 0px 12px;
      width: 240px;
      height: 160px; } }
  @media (min-width: 1120px) {
    .repair__slider-img {
      padding: 17px 0 0px 44px; } }
  @media (min-width: 768px) {
    .repair__slider--hidden {
      display: none; } }
  @media (min-width: 1440px) {
    .repair__slider:nth-child(4) {
      display: block; } }
  @media (min-width: 768px) {
    .repair__btn-img {
      margin-right: 17px; } }
  @media (min-width: 768px) {
    .repair__slider--visible {
      display: block; } }
  @media (min-width: 768px) {
    .repair__list {
      display: flex;
      flex-wrap: wrap; } }
  .repair__span-btn {
    position: absolute;
    right: 20px;
    bottom: 16px; }
    @media (min-width: 768px) {
      .repair__span-btn {
        right: 11px;
        bottom: 13px; } }
    @media (min-width: 1120px) {
      .repair__span-btn {
        right: 14px;
        bottom: 15px; } }
