.modal__feedback {
  visibility: hidden;
  width: 490px;
  height: 940px;
  box-shadow: 1px 0px 20px #dddddd;
  position: fixed;
  top: 0;
  right: 0;
  background: #FFFFFF; }
  .modal__feedback-cont {
    padding-bottom: 16px; }
  .modal__feedback-title {
    font-size: 28px;
    padding: 24px 0;
    position: relative;
    margin-left: 12px;
    font-weight: 700; }
  .modal__feedback-title::before {
    content: "";
    width: 4px;
    height: 40px;
    border-top-right-radius: 4px;
    border-bottom-right-radius: 4px;
    background-color: #41f6d7;
    position: absolute;
    left: -11px;
    top: 19px; }
  .modal__feedback--button {
    display: flex;
    justify-content: space-around;
    align-items: center;
    margin-left: 25px;
    width: 119px;
    height: 32px;
    margin-top: 16px;
    margin-bottom: 16px;
    margin-left: auto;
    background: #FF3E79;
    border-style: none;
    border-radius: 16px;
    font-weight: bold;
    font-size: 12px;
    line-height: 24px;
    letter-spacing: -0.15px;
    text-transform: uppercase;
    color: #fff;
    cursor: pointer; }
  .modal__feedback-inp {
    width: 376px;
    height: 48px;
    border-radius: 9px 9px;
    padding-left: 16px;
    margin: 0 32px;
    border: 1px solid #EAEAEA; }
  .modal__feedback-message {
    width: 376px;
    height: 118px;
    border-radius: 9px 9px;
    border: 1px solid #EAEAEA;
    padding: 12px 0 0 16px;
    margin-left: 32px; }
  .modal__feedback--text {
    width: 376px;
    height: 48px;
    margin: 16px 32px 40px; }
  .modal__feedback--text > span {
    color: #FF3E79; }
  .modal__feedback__close-button {
    margin-left: -50px;
    margin-top: 40px; }

@media (min-width: 320px) {
  .modal__feedback {
    width: 320px;
    background: #FFFFFF; }
    .modal__feedback-inp {
      width: 273px;
      margin: 0 16px; }
    .modal__feedback--text {
      width: 286px;
      height: 64px; }
    .modal__feedback-message {
      margin: 0 16px;
      width: 271px; }
    .modal__feedback-button {
      margin-top: 24px;
      margin-bottom: 40px;
      margin-left: 30px;
      position: absolute; }
    .modal__feedback__close-button {
      margin-left: 10px;
      margin-top: 6px; } }

.modal__feedback__close-button {
  margin-left: 10px;
  margin-top: 7px; }
  @media (min-width: 1440px) {
    .modal__feedback__close-button {
      margin-left: -50px;
      margin-top: 40px; } }
  @media (min-width: 768px) {
    .modal__feedback__close-button {
      margin-left: -50px;
      margin-top: 40px; } }
