a {
  text-decoration: none; }

.btn__close-menu {
  padding-right: 16px; }
  @media (min-width: 1120px) {
    .btn__close-menu {
      display: none; } }

.btn__menu-search {
  padding-left: 78px; }
  @media (min-width: 1120px) {
    .btn__menu-search {
      padding-left: 128px; } }

a {
  color: #1B1C21;
  letter-spacing: -0.2px; }

.aside__header {
  display: flex;
  padding: 23px 24px 0; }

.aside__nav {
  padding: 47.46px 24px 0; }

.aside__item {
  padding-bottom: 32px; }

.aside__link {
  position: relative;
  font-weight: 400;
  color: #1B1C21; }

.aside__link:focus {
  color: #7E7E82; }

.aside__link:focus::before {
  content: "";
  width: 4px;
  height: 32px;
  border-top-right-radius: 4px;
  border-bottom-right-radius: 4px;
  background-color: #41f6d7;
  position: absolute;
  left: -25px; }

.footer {
  margin: 43px 24px 0;
  display: flex;
  flex-direction: column; }
  .footer__icons {
    width: 150px;
    display: flex;
    justify-content: space-between; }
  .footer__mail {
    padding-top: 16px;
    width: 106px; }
  .footer__phone {
    padding: 12px 0 40px 0;
    font-weight: 700;
    font-size: 24px;
    line-height: 32px; }

.lang__items {
  display: flex;
  justify-content: flex-start;
  padding-bottom: 32px; }

.lang__item {
  padding-right: 7px; }

.lang__link:focus {
  color: #7E7E82; }

.wraper {
  background: #FFFFFF;
  width: 320px;
  position: fixed;
  display: none;
  box-shadow: 1px 0px 20px #dddddd; }
  @media (min-width: 1440px) {
    .wraper {
      display: block; } }
